function Child() {
	const divRef = useRef(null);
	useLayoutEffect(() => {
		divRef.current.addEventListener('click', () => {
			console.log('Child div');
		}, { capture: true });
		divRef.current.addEventListener('click', () => {
			console.log('Child div');
		});
	}, []);
	const handleClick = () => {
		console.log('Child');
	};
	const handleClickCapture = () => {
		console.log('Child');
	}
	return <div onClick={handleClick} onClickCapture={handleClickCapture} ref={divRef} style={{ backgroundColor: 'red' }}>
		I'm Child component.
	</div>
}

function App() {
	const divRef = useRef(null);
	useLayoutEffect(() => {
		divRef.current.addEventListener('click', () => {
			console.log('App div');
		}, { capture: true });
		divRef.current.addEventListener('click', () => {
			console.log('App div');
		});
	}, []);
	const handleClick = () => {
		console.log('App ');
	};
	const handleClickChild = () => {
		console.log('handleClickChild');
	};
	const handleClickCapture = () => {
		console.log('App ');
	}
	return <div onClick={handleClick} onClickCapture={handleClickCapture} ref={divRef}>
		<Child onClick={handleClickChild}/>
	</div>
}


export default App;